	module.exports = {"data":{"markdownRemark":{"html":"<h2>Algebra of Logic</h2>\n<p><em>Algebra of Logic</em> or <em>Boolean algebra</em> is a branch of mathematics. It deals with variables and their truth value. The variables have two possible states – <code>true</code> or <code>false</code>.</p>\n<p>It was first introduced by George Boole in his book The Mathematical Analysis of Logic (1847).</p>\n<p>Boolean algebra is fundamental to the development of digital electronics. It is responsible for making possible all modern computing.</p>\n<p>The three basic operations in Boolean Algebra are <code>AND</code>, <code>OR</code>, and <code>NOT</code>. Consider two boolean variables <code>x</code> and <code>y</code>:</p>\n<ul>\n<li><code>x AND y</code> is true if and only if both <code>x</code> and <code>y</code> are true</li>\n<li><code>x OR y</code> is true if and only if either of <code>x</code>, <code>y</code> are true</li>\n<li><code>NOT x</code> is true if and only if <code>x</code> is false and vice versa</li>\n</ul>\n<h3>More Information:</h3>\n<ul>\n<li>The Calculus of Logic: <a href='http://www.maths.tcd.ie/pub/HistMath/People/Boole/CalcLogic/CalcLogic.html' target='_blank' rel='nofollow'>George Boole</a></li>\n<li>Boolean algebra article: <a href='https://en.wikipedia.org/wiki/Boolean_algebra' target='_blank' rel='nofollow'>Wikipedia</a></li>\n</ul>","fields":{"slug":"/logic/algebra-of-logic/"},"frontmatter":{"title":"Algebra of Logic"}}},"pathContext":{"slug":"/logic/algebra-of-logic/"}}

